import React from "react"
import { Link } from "gatsby"
import styled, { keyframes, css } from "styled-components"

const FadeIn = keyframes`
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
`

const StyledHeader = styled.header`
  background-color: ${props =>
    props.transparent ? "rgba(255, 255, 255, 0)" : "rgba(255, 255, 255, 0.5)"};
  padding: 16px 32px;
  font-size: ${props => props.theme.fontSize.font19};
  box-shadow: ${props => (props.transparent ? "" : "0px 0px 4px 2px #3a3a3a")};
  height: 62px;
  color: ${props => (props.home ? "#000000" : "#ffffff")};
  width: 100%;
  position: absolute;
  animation: ${props =>
    props.home
      ? css`
          ${FadeIn} 0.5s ease-in
        `
      : ""};

  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    text-align: right;
  }
`

const Home = styled.li`
  position: absolute;
  left: 32px;
  top: 0px;
  cursor: pointer;
  transition: color 0.3s;
  font-size: ${props => props.theme.fontSize.font27};

  &:hover {
    color: ${props => props.theme.colours.primary};
  }

  a {
    color: inherit;
    text-decoration: none;
  }
`

const NavItem = styled.li`
  display: inline-block;
  margin-left: 36px;
  cursor: pointer;
  padding-bottom: 2px;
  font-weight: 300;

  &:hover {
    border-bottom: 3px solid ${props => props.theme.colours.primary};
  }

  a {
    color: inherit;
    text-decoration: none;
  }

  &:first-child {
    margin-left: 0px;
  }
`

const Header = props => (
  <StyledHeader transparent={props.transparent} home={props.home}>
    <ul>
      <Home>
        <Link to="/">
          <strong style={{ fontSize: "42px" }}>R</strong>uth
        </Link>
      </Home>
      <NavItem active={props.location === "/experience"}>
        <Link to="/experience">Experience</Link>
      </NavItem>

      <NavItem active={props.location === "/blog"}>Blog</NavItem>
      <NavItem active={props.location === "/contact"}>Contact</NavItem>
    </ul>
  </StyledHeader>
)

export default Header
